.app-wrapper {
  display: grid;
  margin: 0 auto;
  width: 1100px;
  grid-template-areas:
"h h"
"n c";
  grid-template-rows: 60px 1fr;
  grid-auto-columns: 2fr 10fr;
  /*grid-gap: 10px;*/
}

.app-header {
  grid-area: h;
  background-color: #68689a;
}

.app-header img {
  width: 70px;
}

.app-navbar {
  grid-area: n;
  background-color: gold;
}

.app-content {
  grid-area: c;
  background-color: orange;
}

.app-content img {
  width: 400px;
}